.Contact {
  width: 100px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  position: fixed;
  right: 0;
  bottom: 0;
}
.Contact h1 {
  font-size: 18px;
  /* writing-mode: vertical-lr; */
  text-orientation: upright;
  text-align: center;
  line-height: 0.8;
  letter-spacing: 0.5px;
}